.section_products_large_page {
    width: calc(100%);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;
    flex: 1;
}

.article_product_large_page {
    width: 220px;
    height: 260px;
    min-width: 180px;
    max-height: 260px;
    border: solid 2px #6400E3;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    margin-left: 20px;
    margin-right: 20px;
}

.article_product_large_page_title {
    width: 90%;
    margin-top: 20px;
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: bolder;
    color: #6400E3;
}

.article_product_large_page_intro {
    width: 90%;
    font-weight: 600;
    color: #535353;
    font-size: 11px;
}

.article_product_large_page_price {
    color: #6400E3;
    text-align: left;
    width: 90%;
    font-weight: bolder;
    font-size: 60px;
    margin: 0;
    padding: 0;
}

.article_product_large_page_button {
    width: 90%;
    height: 23px;
    margin-top: 0px;
    margin-bottom: 15px;
    border-radius: 6px;
    cursor: pointer;
    color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding-left: 10px;
    font-size: 12px;
    font-weight: bold;
    background: #6400E3;
    text-decoration: none;
}

.info-grey {
    background-color: #C7C7C7;
}

.article_product_large_page_product {
    width: 90%;
    height: 20px;
    padding-left: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 6px;
    font-size: 10px;
    font-weight: bold;
}

.article_product_large_progress {
    position: absolute;
    width: 110px;
    height: 20px;
    background: #6400E3;
    top: -10px;
    border-radius: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 10px;
    font-weight: 600;
}
